body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ingredient-list, .recipe-ctn, .submit-ctn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

html, body {
  height: 100%;
  width: 100%;
  font-family: "Dosis", sans-serif;
  background: transparent;
  scroll-behavior: smooth;
}

#root {
  width: 100%;
  height: auto;
  background: -webkit-linear-gradient(rgba(138, 169, 208, 0.9), rgba(138, 169, 208, 0.9)), url(/recipe/static/media/utensils.0b92cccf.png);
  background: linear-gradient(rgba(138, 169, 208, 0.9), rgba(138, 169, 208, 0.9)), url(/recipe/static/media/utensils.0b92cccf.png);
  background-size: 230px;
  background-repeat: repeat;
}

#container {
  padding-top: 40px;
}

.image-credit {
  margin: 20px;
  color: #FFF;
}

.credit-link {
  color: #FFF;
}
.credit-link:hover {
  color: #323232;
  text-decoration: none;
}

/* ------------------------ */
/* ---- FORM CONTAINER ---- */
/* ------------------------ */
h1 {
  font-family: "Dosis", sans-serif;
}

.form-container {
  width: 600px;
  padding: 3% 3% 2% 2%;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 5px;
}
@media only screen and (max-width: 800px) {
  .form-container {
    width: 80%;
  }
}

.form-description {
  text-align: center;
}

.col-form-label {
  font-family: "Dosis", sans-serif;
  font-size: 18px;
}

.form-group {
  padding: 5px 20px;
  font-family: "Open Sans", sans-serif;
}

.space {
  padding: 0;
}

.form-check-label {
  font-size: 16px;
  font-family: "Dosis", sans-serif;
}

/* Submit Button */
.btn-secondary {
  font-family: "Dosis", sans-serif;
  width: 120px;
  background-color: #7d98bc;
  border: none;
  margin-top: 8px;
}
.btn-secondary:hover {
  background-color: #96b2d5;
}

/* ------------------------ */
/* ---- SEARCH RESULTS ---- */
/* ------------------------ */
#list {
  scroll-behavior: smooth;
}

.recipe-ctn {
  padding: 20px;
}

.recipe-item {
  cursor: pointer;
  padding: 20px;
  background-color: #F8F8F8;
  border-radius: 5%;
  margin: 5px;
}
.recipe-item:hover {
  background-color: #EFEFEF;
}

.recipe-img {
  width: 100%;
  height: 200px;
  border-radius: 5%;
  object-fit: cover;
}

.recipe-title {
  padding: 20px 0 0 0;
}

/* Pagination */
.pagination {
  border: solid 0.5px #FFF;
}

.nav-pag {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.page-item {
  cursor: pointer;
}

.page-link {
  color: #96b2d5;
}

li.active .page-link {
  background-color: #7d98bc !important;
  border-color: #7d98bc !important;
}

.hide {
  display: none;
}

/* ------------------------- */
/* ---- MODAL CONTAINER ---- */
/* ------------------------- */
.modal-body {
  text-align: center;
}

.modal-image {
  width: 50%;
}

.recipe-info {
  margin-top: 10px;
}

/* Ingredients List */
.table {
  width: 80%;
  margin: 20px 0;
}

/* Instructions */
.modal-text {
  text-align: left;
}

.recipe-instructions {
  padding: 10px 10px;
}

h5 {
  padding-left: 25px;
}

.close-btn {
  background-color: #7d98bc;
  border: none;
}
.close-btn:hover {
  background-color: #96b2d5;
}

